.wrapper{
    display: flex;
    overflow-x: scroll;
    width: 100%;
}

.wrapper::-webkit-scrollbar{
    background-color: black;
    border-radius: 5px;
    height: 8px;
    width: 100%;
}

.wrapper::-webkit-scrollbar:hover{
    background: linear-gradient(#5156E6 55%, #74EDF2);
}

.item{
    margin-right: 25px;
}